* {
    box-sizing: border-box;
}

html {
    font-family: sans-serif;
}

body {
    margin: 0;
}

h1, h2, h3, h4, h5, h6, p {
    margin: 0;
}

button {
    border: unset;
    background: unset;
    padding: none;
    outline: none;
    border: 1px solid black;
    border-radius: 5px;
} button:hover {cursor: pointer;}

input, textarea {
    width: 200px;
    outline: none;
}

input, select {
    padding: 5px;
    padding-left: none;
    border: unset;
    background: unset;
    border-bottom: 1px solid black;
}

textarea {
    height: 75px
}

a {
    text-decoration: none;
    color: unset;
}